<template>
  <div class="download-container"> 
    <ul class="head-infos">
      <li class="version">version {{ object.version }}</li>
      <li class="release-date">{{ object.releaseDate }}</li>
      <li class="file-size">{{ object.fileSize }}  </li>
    </ul>
    <p class="info-toggle" v-on:click="toggleShowInfos()">Show more infos</p>
    <ul v-bind:class="[showInfos ? 'show-infos' : 'display-none']" class="info-list">
      <li class="info-item" v-for="(item, index) in object.infoItems" :key="index">{{ item }}</li>
    </ul>
    <p class="download" v-on:click="openDownloadLink()">Click to Download</p>
  </div>
</template>

<script>
export default {
  name: 'DownloadObject',
  props: ['object'],
  data () {
    return {
      showInfos: false,
    }
  },
  methods: {
    openDownloadLink: function () {
      window.open(this.object.downloadLink)
    },
    toggleShowInfos: function () {
      if (this.showInfos) {
        this.showInfos = false
      } else {
        this.showInfos = true
      }
    }
  }
}
</script>

<style scoped>
.display-none {
  display: none;
}
.download, .info-toggle {
  margin: 4% 2%;
}
.download:hover {
  color: blue;
  text-shadow: #000 1px 1px 1px;
}
.info-toggle {
  margin-bottom: 2%;
}
.info-toggle:hover {
  color: rgb(242, 226, 0);
  text-shadow: #000 1px 1px 1px;
}
.info-list {
  list-style-type: circle;
  margin-top: 0;
}
.info-item {
  font-weight: normal;
  text-align: left;
}
.head-infos {
  list-style: none;
  padding: 0;
  margin: 4%;
}
.head-infos li {
  display: inline-block;
  margin: 0% 3%;
}
.download-container {
  float: center;
  margin: 0% 2% 10%;
  padding: 2%;
  font-weight: 700;
  font-size: 2rem;
  background-color: #eee;
  border-radius: 20px;
  width: 96%;
  box-sizing: border-box;
  transition: box-shadow 0.3s;
}
.download-container:hover {
  box-shadow: #000 0px 0px 20px;
  background: #fff;
}

@media(max-width: 1000px) {
  .download-container {
    width: 96%;
  }
}

@media(max-width: 750px){
	.download-container {
    font-size: 4vw;
  }
}
</style>