<template>
  <div class="tower-container">
    <div class="top">
      <div class="heading-img">
        <p class="heading">{{ object.name }}</p>
        <img 
        :src="require(`../assets/grassland/${object.name.toLowerCase().replace(' ', '_')}.png`)" 
        :alt="object.name"
        >
      </div>

      <div class="casual">
        <p>{{ object.shootingType }}</p>
        <p>shoots {{ object.initialStats.shootRate }} times per second</p>
        <p>range: {{ object.initialStats.radius }}</p>
        <p v-if="object.specialUpgradeStat">{{ object.specialUpgradeStat }}: {{ object.initialStats.special }}</p>
      </div>
    </div>

    <div class="upgrades">
      <p class="show-upgrades" v-on:click="toggleShowUpgrades()">Show upgrades</p>
      <ul :class="[showUpgrades ? 'upgrade-list' : 'display-none']">
        <li v-for="upgrade in object.upgrades" :key="upgrade.name">
          {{ upgrade.name }} ({{ upgrade.costs }} coins) - {{ upgrade.statName }}: {{ upgrade.values[0] }} -> {{ upgrade.values[1] }}
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Tower',
  props: ['object'],
  data () {
    return {
      showUpgrades: false
    }
  },
  methods: {
    toggleShowUpgrades: function () {
      if (this.showUpgrades) {
        this.showUpgrades = false
      } else {
        this.showUpgrades  = true
      }
    }
  }
}
</script>

<style scoped>
.display-none {
  display: none
}
.tower-container {
  font-size: .8em;
  font-weight: 600;
  overflow: hidden;
  margin: 1% 1.66%;
  float: left;
  width: 30%;
  box-sizing: border-box;
  background-color: #eee;
  border-radius: 20px;
}
.top {
  width: 100%;
}
.heading-img, .casual {
  float:left;
  width: 50%;
  padding: 0 5%;
  box-sizing: border-box;
}
.casual {
  padding: 2% 5%;
}
.upgrades {
  float:left;
  width: 90%;
  margin: 0 5%;
  box-sizing: border-box;
  text-align: left;
}
.show-upgrades {
  text-align: center;
  margin: 0 0 6% 0;
}
.show-upgrades:hover {
  color: rgb(242, 226, 0);
  text-shadow: #000 1px 1px 1px;
}
.upgrade-list {
  text-align: left;
}
@media(max-width: 870px) {
  .tower-container {
    width: 90%;
    margin: 2%;
    float: left;
  }
}
</style>