<template>
  <div id="main-footer">
    <p id="text">Puzzle Defense - {{ getRandomText() }}</p>
  </div>
</template>

<script>
export default {
  name: 'Footer',
  data() {
    return {
      texts: [
        "the best game you've ever played",
        "created with love",
        "it's your destiny to protect it",
      ]
    }
  },
  methods: {
    getRandomText () {
      return this.texts[Math.floor(Math.random() * this.texts.length)]
    }
  },
}
</script>

<style scoped>

#main-footer {
	background: #222;
	color:#fff;
	text-align: center;
  height: 8vh;
  position: relative;
  padding: 2vh 0;
}
#text {
  font-size: 100%;
  font-family: Calibri, Arial, sans-serif;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>