<template>
  <div>
    <div id="app">
      <div class="header">
        <h1 id="title">Puzzle Defense</h1>
        <p id="credits">a game by Finn Kuschetzki and Hauke Cordes</p>
      </div>
      <div id="nav">
        <NavBar :released="released"/>
      </div>
      <router-view v-if="released"/>
      <ReleaseCountdown :releaseDate="releaseDate" v-else/>
    </div>
    <div id="footer">
      <Footer/>
    </div>
  </div>
</template>

<script>
import NavBar from './components/NavBar'
import Footer from './components/Footer'
import ReleaseCountdown from './views/ReleaseCountdown'
export default {
  name: 'App',
  components: {
    NavBar,
    Footer,
    ReleaseCountdown,
  },
  data () {
    return {
      releaseDate: new Date(2021, 2, 27, 21, 0)
    }
  },
  computed: {
    released () {
       return (new Date().getTime() - this.releaseDate.getTime() >= 0)
    }
  },
}
</script>

<style>
#app {
  font-family: Calibri, Arial, sans-serif;
  font-size: 1.5rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000;
  position: relative;
  min-height: 88vh;
  overflow: hidden;
}
body {
  background: #fff;
}
#title {
  font-size: 2.5em;
  margin: 0;
  padding: .2em 0 0;
}
#credits {
  font-size: 1em;
  margin: auto;
  padding: 0 5% .8em;
}
.header {
	background-image:url('./assets/header.png');
	background-position: center;
	margin-bottom: 0;
	text-align: center;
  background-repeat: repeat-x;
  -webkit-appearance: none;
}

@media(max-width: 870px) {
  #app {
    font-size: 1.3rem;
  }
}
@media(max-width: 500px) {
