<template>
  <div>
    <p class="info">Version 2 of PuzzleDefense will be released at:</p>
    <p class="releaseDate">{{ getWeekday(releaseDate.getDay()) }}   {{ releaseDate.getDate().toString().padStart(2, "0") }}.{{ (releaseDate.getMonth()+1).toString().padStart(2, "0") }}. {{ releaseDate.getHours().toString().padStart(2, "0") }}:{{ releaseDate.getMinutes().toString().padStart(2, "0") }}</p>
    <p class="countdown" id="countdown"> {{ days.toString().padStart(2, "0") }} : {{ hours.toString().padStart(2, "0") }} : {{ minutes.toString().padStart(2, "0") }} : {{ seconds.toString().padStart(2, "0") }} </p>
  </div>
</template>

<script>
export default {
  name: 'ReleaseCountdown',
  props: ['releaseDate'],
  data () {
    return {
      countdown: false,
      toWait: this.releaseDate - Date.now(),
      days: Math.floor(this.toWait / (60 * 60 * 24 * 1000)),
      hours: Math.floor((this.toWait - this.days * (60 * 60 * 24 * 1000)) / (60 * 60 * 1000)),
      minutes: Math.floor((this.toWait - this.days * (60 * 60 * 24 * 1000) - this.hours * (60 * 60 * 1000)) / (60 * 1000)),
      seconds: Math.floor((this.toWait - this.days * (60 * 60 * 24 * 1000) - this.hours * (60 * 60 * 1000) - this.minutes * (60 * 1000)) / (1000)),
      changer: false,
    }
  },
  created() {
    var self = this
    setInterval(function () {
      self.toWait = self.releaseDate - Date.now();
      self.refresh()
      self.days = Math.floor(self.toWait / (60 * 60 * 24 * 1000));
      self.hours = Math.floor((self.toWait - self.days * (60 * 60 * 24 * 1000)) / (60 * 60 * 1000));
      self.minutes = Math.floor((self.toWait - self.days * (60 * 60 * 24 * 1000) - self.hours * (60 * 60 * 1000)) / (60 * 1000));
      self.seconds = Math.floor((self.toWait - self.days * (60 * 60 * 24 * 1000) - self.hours * (60 * 60 * 1000) - self.minutes * (60 * 1000)) / (1000));
      self.changeColor();
      if (self.days == -1) {
        self.days = 0;
        self.hours = 0;
        self.minutes = 0;
        self.seconds = 0;
      }
    }, 1000)
  },
  methods: {
    getWeekday: function (day) {
      switch (day) {
        case 1:
          return "Monday"
        case 2:
          return "Tuesday"
        case 3:
          return "Wednesday"
        case 4:
          return "Thursday"
        case 5:
          return "Friday"
        case 6:
          return "Saturday"
        case 0:
          return "Sunday"
      }
    },
    changeColor: function () {
      if (this.changer == false) { 
        document.getElementById("countdown").style.textShadow = "0px 0px 0px #fff";
        this.changer = true
      } else {
        document.getElementById("countdown").style.textShadow = "2px 2px 0px #aaa";
        this.changer = false
      }
    },
    refresh: function () {
      if (this.seconds <= 0 && this.toWait <= 10000) {
        location.reload()
      }
    }
  }
}
</script>

<style scoped>
.info, .releaseDate, .countdown-str, .countdown {
  font-size: 2em;
  overflow: hidden;
  float: left;
  width: 100%;
  margin: 4% 0 0 0;
  box-sizing: border-box;
}
.countdown {
  font-weight: 700;
  font-size: 5em;
  margin: 5% 0;
  float: left;
  width: 100%;
}
@media(max-width: 870px) {
  .info, .releaseDate {
    font-size: 1.2em;
  }
  .countdown {
    font-size: 2.2em;
    margin: 10% 0;
  }
}
</style>