<template>
  <div class="container">

    <div class="content-overview" id="co">
      <ul class="table-of-contents">
        <li class="first-level-content"><a href="#shortcuts" class="content-link">Shortcuts</a></li>
        <li class="first-level-content"><a href="#tower-defense" class="content-link">Tower Defense</a></li>
        <ul class="td-contents">
          <li class="second-level-content"><a href="#levels" class="content-link">Levels</a></li>
          <li class="second-level-content"><a href="#tower" class="content-link">Tower</a></li>
          <li class="second-level-content"><a href="#enemies" class="content-link">Enemies</a></li>
          <li class="second-level-content"><a href="#miner" class="content-link">Miner</a></li>
        </ul>
        <li class="first-level-content"><a href="#mini-games" class="content-link">Mini Games</a></li>
      </ul>
    </div>

    <div class="head-quote">
      <hr class="separator">
        <p class="quote-text">"It's all about two things! Get coins by mastering the Mini Games and be strategic, so no enemies run through your Tower Defense area."</p>
        <p class="quote-info">~ Finn Kuschetzki, Developer, Administrator</p>
      <hr class="separator">
    </div>

    <div class="content">

      <div class="big-section" id="shortcuts"> 
        <a class="big-section-heading" href="#co" id="shortcuts-heading">Shortcuts</a>
        <div class="text-body">
          <ul class="short-text">
            <p class="text">ESC: opens the options menu</p>
            <p class="text">F11: toggles fullscreen</p>
            <p class="text">F12: adjusts the aspect ration to 16:9</p>
          </ul>
        </div>
      </div>

      <div class="big-section" id="tower-defense">
        <a class="big-section-heading" href="#co">Tower Defense</a>

        <div class="section" id="levels">
          <a class="section-heading" href="#co">Levels:</a>
          <Level v-for="level in levelNames" :key="level" :levelName="level" class="level-object"/>
        </div>

        <div class="section" id="tower">
          <a class="section-heading" href="#co">Tower:</a>
          <Tower v-for="tower in towerList" :key="tower.name" :object="tower" class="tower-object"/>
        </div>

        <div class="section" id="enemies">
          <a class="section-heading" href="#co">Enemies:</a>
          <Enemy v-for="enemy in enemyList" :key="enemy.name" :object="enemy"/>
        </div>

        <a class="section-heading" href="#co" id="miner-heading">Miner:</a>
        <div class="section" id="miner">
          <ul class="miner-functions">
            <li>
              <div class="miner-section">
                <p>- generates hearts that can save you -</p>
                <p class="show-upgrade-button" v-on:click="toggleShowMinerHeartUpgrades">Show upgrades</p>
                <ul :class="[showMinerHeartUpgrades ? 'show-miner-upgrade' : 'display-none']" class="miner-upgrade" id="hearts">
                  <li v-for="(upgrade, index) in miner.heartUpgrades" :key="index">
                    price: {{ upgrade.costs }} -> {{ upgrade.statName }}: {{ upgrade.value }}
                  </li>
                </ul>
              </div>
            </li>
            <li>
              <div class="miner-section">
                <p>- generates coins each round -</p>
                <p class="show-upgrade-button" v-on:click="toggleShowMinerCoinUpgrades">Show upgrades</p>
                <ul :class="[showMinerCoinUpgrades ? 'show-miner-upgrade' : 'display-none']" class="miner-upgrade" id="coins">
                  <li v-for="(upgrade, index) in miner.coinUpgrades" :key="index">
                    price: {{ upgrade.costs }} -> gives you {{ upgrade.value }} coins per round
                  </li>
                </ul>
              </div>
            </li>
          </ul>
        </div>
      
      </div>

      <div class="big-section" id="mini-games">
        <div>
          <a class="big-section-heading" href="#co">Mini Games</a>
        </div>

        <div class="mini-left">
          <MiniGame v-for="miniGame in miniGamesLeft" :key="miniGame.name" :object="miniGame"/>
        </div>
        <div class="mini-right">
          <MiniGame v-for="miniGame in miniGamesRight" :key="miniGame.name" :object="miniGame"/>
        </div>
        

      </div>

    </div>

    <a href="#co"> <img src="../assets/upload.svg" alt="button-up" class="back-to-top"> </a>

  </div>
</template>

<script>
import Level from '../components/Level'
import Tower from '../components/Tower'
import Enemy from '../components/Enemy'
import MiniGame from '../components/MiniGame'
export default {
  name: 'Wiki',
  components: {
    Level,
    Tower,
    Enemy,
    MiniGame,
  },
  data () {
    return {
      showMinerHeartUpgrades: false,
      showMinerCoinUpgrades: false,
      levelNames: [
        'grassland',
        'night-city',
        'islands',
        'desert',
        'hell'
      ],
      towerList: [
        {
          name: 'Single Shooting',
          shootingType: 'single bullets hitting one enemy',
          initialStats: {costs: 50, radius: 150, shootRate: 2},
          specialUpgradeStat: false,
          upgrades: [
            {name: 'Faster Shooting', costs: 50, statName: 'shots per second', values: [2, 4]},
            {name: 'Increased Radius', costs: 70, statName: 'radius', values: [150, 225]},
            {name: 'Gun Fire', costs: 250, statName: 'shots per second', values: [4, 6]}
          ]
        },
        {
          name: 'Special Shooting',
          shootingType: 'single bullets destroying multiple layers of an enemy',
          initialStats: {costs: 150, radius: 150, shootRate: 1, special: 1},
          specialUpgradeStat: 'pops layer(s)',
          upgrades: [
            {name: 'Pops Two Layers', costs: 50, statName: 'pops layers', values: [1, 2]},
            {name: 'Increased Radius', costs: 100, statName: 'radius', values: [150, 300]},
            {name: 'Faster Shooting', costs: 100, statName: 'shots per second', values: [1, 2]},
            {name: 'Pops Three Layers', costs: 100, statName: 'pops layers', values: [2, 3]}
          ]
        },
        {
          name: 'Rocket Shooting',
          shootingType: 'rockets hitting all enemies in missiles range',
          initialStats: {costs: 150, radius: 150, shootRate: 1, special: 70},
          specialUpgradeStat: 'projectile radius',
          upgrades: [
            {name: 'Faster Shooting', costs: 150, statName: 'shots per second', values: [1, 2]},
            {name: 'Shoot Even Faster', costs: 200, statName: 'shots per second', values: [2, 4]},
            {name: 'Increased Projectile Radius', costs: 200, statName: 'projectile impact radius', values: [70, 100]},
            {name: 'Even Bigger Projectile Radius', costs: 200, statName: 'projectile impact radius', values: [100, 130]}
          ]
        }
      ],
      enemyList: [
        {tier: 1, speed: 2, health: 1},
        {tier: 2, speed: 4, health: 2},
        {tier: 3, speed: 6, health: 3},
        {tier: 4, speed: 8, health: 4},
        {tier: 5, speed: 10, health: 5},
        {tier: 'special', speed: 1, health: '50+'}
      ],
      miner: {
        heartUpgrades: [
          {costs: 100, statName: 'generates up to', value: 10},
          {costs: 50, statName: 'generates up to', value: 20},
          {costs: 50, statName: 'generates up to', value: 30},
          {costs: 50, statName: 'generates up to', value: 40},
          {costs: 50, statName: 'generates up to', value: 50},
          {costs: 100, statName: 'generated per round', value: 10},
          {costs: 100, statName: 'generated per round', value: 15},
          {costs: 100, statName: 'generated per round', value: 20},
        ],
        coinUpgrades: [
          {costs: 'free', value: 10},
          {costs: 75, value: 15},
          {costs: 75, value: 20},
          {costs: 75, value: 25},
          {costs: 150, value: 30},
          {costs: 150, value: 40},
          {costs: 150, value: 50},
        ],
      },
      miniGamesLeft: [
        {
          name: 'The Great Change',
          coins: 50,
          firstPossibleRound: 10,
          description: `
            Click on the colored squares. When clicked on, the square itself 
            and the directly surrounding squares (not diagonally) will change their color. 
            Each block can be two colors, to win you need to change all squares to have the same color.
          `
        },
        {
          name: 'Car Park',
          coins: 30,
          firstPossibleRound: 1,
          description: `
            The goal is to move the red car through the green gate on the right side. 
            To achieve this you can select a car and move it forwards or backwards (according to its orientation). 
            One can select a car by clicking on it (an activated car gets a white border) and than move it using the WASD keys or arrow keys."
          `
        },
        {
          name: 'The Quiz',
          coins: 20,
          firstPossibleRound: 1,
          description: `
            You will have to answer three question. All three of these belong to one topic. 
            Submit your answer by clicking on it. Don't worry if you can't answer all three questions. 
            You will still get a part of the reward:  two correct -> 10,  one correct -> 5
          `
        },
        {
          name: 'Geography',
          coins: 10,
          firstPossibleRound: 1,
          description: `
            You get a map without any borders. The task is to identify the marked city. 
            Type in the name of the shown city but use it's English name! Press ENTER to check, if you fail you won't get a second chance.
          `
        },
        {
          name: 'Minefield',
          coins: 40,
          firstPossibleRound: 10,
          description: `
            You get a minefield with all cells being covered. Left-click on a cell to uncover it, if there is a bomb you will immediately lose. 
            An empty field can contain a number representing the number of bombs directly and diagonally adjacent to it.
            Use this to calculate where a bomb is located, you can mark a possible bomb by right-clicking on the field. 
            (This is only a visual for you, remove by right-clicking again!) 
            If you fail with only three or less fields uncovered you'll get another chance. 
            Win by uncovering all fields except those containing bombs!
          `
        }
      ],
      miniGamesRight: [
        {
          name: 'Remember It',
          coins: 30,
          firstPossibleRound: 5,
          description: `
            You need to remember a sequence of blocks flashing. After the blocks stopped flashing, click them in the exact same order as before. 
            A block will indicate that it got clicked with another short blink. 
            You will lose immediately with any mistake and win as you clicked the whole sequence correctly.
          `
        },
        {
          name: 'Stacking',
          coins: 70,
          firstPossibleRound: 40,
          description: `
            Blocks will fall from the top. 
            You can move those pieces left/right using the A/D keys or the arrow keys. 
            Press W or the UP arrow-key to rotate the piece clockwise. 
            Your goal is to achive 3 perfect rows (a row is perfect if all of its squares are filled), 
            but if one block stacks over the red line you will fail.
          `
        },
        {
          name: 'Guess The Word',
          coins: 40,
          firstPossibleRound: 1,
          description: `
            Guess the word displayed, if you don't get it yet hit space to reveal a new letter. 
            But it does not come without any costs, the more letters you reveal, the less coins you get.
            In case you know a possible solution type and hit enter to guess. If you are wrong, you are free to guess again.
          `
        },
        {
          name: '128 - The Game',
          coins: 30,
          firstPossibleRound: 5,
          description: `
            Move the blocks around using the WASD keys or the arrow keys. Notice that all blocks on the field will perform this action. 
            If two blocks with the same number stack onto another they'll collide and merge into one block containing the sum of their values. 
            To make it more challenging you can't move in one direction twice in a row. You win by creating a block with the value of 128!
          `
        },
        {
          name: 'Invisible Maze',
          coins: 30,
          firstPossibleRound: 5,
          description: `
            Find your way trough the maze, navigate using the arrow keys or the WASD keys. 
            The borders will show up frequently but always only for a short period of time. 
            Keep them in mind and find your way towards the exit marked by an arrow.
          `
        }
      ]
    }
  },
  methods: {
    toggleShowMinerHeartUpgrades: function () {
      if (this.showMinerHeartUpgrades) {
        this.showMinerHeartUpgrades = false
      } else {
        this.showMinerHeartUpgrades = true
      }
    },
    toggleShowMinerCoinUpgrades: function () {
      if (this.showMinerCoinUpgrades) {
        this.showMinerCoinUpgrades = false
      } else {
        this.showMinerCoinUpgrades = true
      }
    }
  }
}
</script>

<style scoped>
.display-none {
  display: none;
}
.back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
}
.container {
 font-family: Calibri;
 color: #000;
 margin: 0 2%;
}
.container * :link , .container * :visited {
  color: #222;
  text-decoration: none;
}
.td-contents ul, .content-overview ul, .content ul, .miner-section ul {
	list-style-type: none;
}
.content-overview {
  background-color: #eee;
  border-radius: 20px;
  float: left;
  width: 20%;
  padding: 0 2% 0 0;
  margin: 2%;
  text-align: left;
  font-weight: 800;
  font-size: 1.1em;
}
.content-overview li{
  display: block; 
}
.content-overview li :hover {
  text-shadow: #000 1px 1px 1px;
}
.td-contents {
  font-weight: 500;
  padding: 0 0 0 10%;
}
.head-quote {
  font-family: 'TimesNewRoman';
  width: 64%;
  float: right;
  margin: 3.5% 5% 2% 5%;
}
.section {
  margin-top: 25px;
}
.section-heading {
  width: 100%;
  float: left;
  font-weight: 800;
  font-size: 1.1em;
  margin: 0;
}
.section-heading:active {
  text-shadow: #000 1px 1px 1px;
}
#levels, #tower, #enemies, #miner {
  float: left;
  width: 90%;
  margin: 1% 5%;
  font-weight: 600;
}
.text {
  display:inline-block;
  width: fit-content;
  margin: 0;
  padding: 1%;
}
.text-body {
  float: left;
  width: 100%;
}
.short-text {
  text-align: center;
  padding: 1em;
  width: fit-content;
  overflow: hidden;
  background-color: #eee;
  border-radius: 20px;
  align-self: center;
  margin: 2% auto;
}
.big-section {
  float: left;
  padding: 0;
  width: 100%;
  margin: 0;
}
.big-section-heading {
  float: none;
  width: 100%;
  font-weight: 1000;
  font-size: 1.75rem;
  margin: 0;
}
.miner-functions {
  background-color: #eee;
  border-radius: 20px;
  width: 60%;
  padding: 1%;
  margin: 0 20% 5% 20%;
}
.show-upgrade-button {
  margin: 0 0 2% 0;
}
.show-upgrade-button:hover {
  color: rgb(242, 226, 0);
  text-shadow: #000 1px 1px 1px;
}
.miner-upgrade li{
  list-style-type: circle;
  text-align: left;
  font-weight: normal;
  margin-top: 0;
}
.mini-left {
  width: 50%;
  float: left;
}
.mini-right {
  width: 50%;
  float: right;
}
@media(max-width: 1400px) {
  .tower-object, .level-object {
    width: 45%;
  }
}
@media(max-width: 1000px) {
  .mini-left, .mini-right {
    float: none;
    width: 100%;
  }
}
@media(max-width: 870px) {
  .content-overview {
    width: 80%;
    margin: 5%;
    padding: 0 10% 0 0;
    text-align: center;
  }
  .td-contents {
  padding: 0;
  }
  .head-quote {
    float: left;
    width: 90%;
    margin: 5%;
  }
  .tower-object, .level-object {
    width: 96%;
  }
  .miner-functions {
    width: 100%;
    margin: 0 0 5% 0;
  }
}
</style>